import { createSlice } from "@reduxjs/toolkit";

const cartSlice = createSlice({
  name: "cart",
  initialState: {
    amount: 0,
    data: [],
  },
  reducers: {
    increment(state, action) {
      if (
        state.data.length === 0 ||
        !state.data.some((e) => e.id === action.payload)
      ) {
        return;
      }
      const curEl = state.data.find((e) => e.id === action.payload);
      console.log(action.payload);
      state.amount++;
      curEl.amount++;
    },
    decrement(state, action) {
      if (
        state.data.length === 0 ||
        !state.data.some((e) => e.id === action.payload)
      ) {
        return;
      }
      const curEl = state.data.find((e) => e.id === action.payload);
      if (curEl.amount === 0) {
        state.data.splice(state.data.indexOf(curEl), 1);
        return;
      }

      state.amount--;
      curEl.amount--;
    },
    addtoCart(state, action) {
      if (state.data.some((e) => e.id === action.payload.id)) return;
      state.data.push(action.payload);
    },
  },
});
export const cartActions = cartSlice.actions;
export default cartSlice.reducer;
